import axios from 'axios'


const requestQueue = []
const responseQueue = []
const globalError = []
const errorMsg = {
  400: '(400)',
  401: '(401)',
  403: '(403)',
  404: '(404)',
  408: '(408)',
  500: '(500)',
  501: '(501)',
  502: '(502)',
  503: '(503)',
  504: '(504)',
  505: 'HTTP(505)'
}

const ajaxPory = {
  addRequestCallback (fn) {
    requestQueue.push(fn) // 
  },
  removeRequestCallback (fn) {
    const index = requestQueue.indexOf(fn)
    if (index !== -1) {
      requestQueue.splice(index, 1)
    }
  },
  addResponseCallback (fn) {
    responseQueue.push(fn)
  },
  removeResponseCallback (fn) {
    const index = responseQueue.indexOf(fn)
    if (index !== -1) {
      responseQueue.splice(index, 1)
    }
  },
  setGlobalError (fn) {
      globalError.push(fn) // 
  },
  /**
   * 
   * @param  {string|array}   url  
   * @param  {Array}   data  
   * @param  {Function} fn   
   * @return {Promise}       ajaxpromise
   */
  ajaxPost (url, data) {
    data = this.callRequest(data)
    return new Promise((r, j) => {
      axios.post(url, data).then(d => {
        // 
        const retCode = this.callResponse(d)
        if (retCode === 0) {
          r(data)
        } else {
          // 
          j(new Error(''))
        }
      }).catch(error => {
        let msg
        if (error && error.response) { // 200
          msg = errorMsg[error.response.status] || ''
        } else {
          msg = '' // 
        }
        globalError.forEach(fn => { // 
          fn.call(this, msg)
        })
        j(new Error(msg))
      })
    })
  },
  ajaxGet (url, data = {}) {
    data = this.callRequest(data)
    return new Promise((r, j) => {
      Object.keys(data).forEach(d => {
        data[d] = encodeURIComponent(data[d])
      })
      const config = {
        params: data
      }
      axios.get(url, config).then(d => {
        const retCode = this.callResponse(d)
        if (retCode === 0) {
          r(data)
        } else {
          // 
          j(new Error(''))
        }
      }).catch(error => {
        let msg
        if (error && error.response) { // 200
          msg = errorMsg[error.response.status] || ''
        } else {
          msg = '' // 
        }
        globalError.forEach(fn => { // 
          fn.call(this, msg)
        })
        j(new Error(msg))
      })
    })
  },
  callRequest (data) {
    const requestQueueClone = requestQueue.slice(0) // 
    while (requestQueueClone.length > 0) {
      let fn = requestQueueClone.shift()
      data = fn(data)
    }
    return data
  },
  callResponse (data) {
    // 
    const responseQueueClone = responseQueue.slice(0) // 
    let retCode = 0
    while (responseQueueClone.length > 0) {
      var fn = responseQueueClone.shift()
      retCode += fn(data)
    }
    return retCode // 
  }
}

export default ajaxPory
